export default "\n@import url(\"https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;400;500&display=swap\");\n/*animated css cards font*/\nion-grid {\n  --ion-grid-padding-sm: 20px;\n  --ion-grid-padding-md: 30px;\n  --ion-grid-padding-lg: 40px;\n  --ion-grid-padding-xl: 50px;\n}\n.cardd {\n  display: flex;\n  height: 100%;\n  width: 100%;\n  border-radius: 10px !important;\n  margin-left: 30px;\n  margin-right: 30px;\n}\n.cardd > .thumb {\n  display: inline-flex;\n  cursor: pointer;\n  width: 50%;\n  height: 100%;\n}\n.fontt {\n  font-family: \"TransRobotics\";\n  font-size: 50px;\n  padding-top: 10px;\n}\n.fonttCard {\n  display: flex;\n  justify-content: center;\n  font-family: \"TransRobotics\";\n  font-size: 25px;\n}\n.rowPadding {\n  padding-top: 10px;\n  padding-bottom: 10px;\n}\n.fonttBtn {\n  font-family: \"TransRobotics\";\n  font-size: 15px;\n}\n.javascrip {\n  --background: #fcf75e;\n  --color: black;\n}\n.pro {\n  background: #00fa9a;\n}\n.de {\n  --background: #536878;\n  --color: black;\n}\n.btnpad {\n  padding-right: 4px;\n}\n/*animated css cards*/\nbody {\n  padding: 0;\n  margin: 0;\n  background-color: #17141d;\n  color: white;\n  font-family: \"DM Mono\", monospace;\n}\na {\n  text-decoration: none;\n}\n.card-list {\n  display: flex;\n  padding: 3rem;\n  overflow-x: scroll;\n}\n.card-list::-webkit-scrollbar {\n  width: 10px;\n  height: 10px;\n}\n.card-list::-webkit-scrollbar-thumb {\n  background: #201c29;\n  border-radius: 10px;\n  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.25), inset -2px -2px 2px rgba(0, 0, 0, 0.25);\n}\n.card-list::-webkit-scrollbar-track {\n  background: linear-gradient(90deg, #201c29, #201c29 1px, #17141d 0, #17141d);\n}\n.card {\n  height: 350px;\n  width: 400px;\n  min-width: 250px;\n  padding: 1.5rem;\n  border-radius: 16px;\n  background: #17141d;\n  box-shadow: -1rem 0 3rem #000;\n  display: flex;\n  flex-direction: column;\n  transition: 0.2s;\n  margin: 0;\n  scroll-snap-align: start;\n  clear: both;\n  position: relative;\n}\n.card:focus-within ~ .card,\n.card:hover ~ .card {\n  transform: translateX(130px);\n}\n.card:hover {\n  transform: translateY(-1rem);\n}\n.card:not(:first-child) {\n  margin-left: -130px;\n}\n.card-header {\n  margin-bottom: auto;\n}\n.card-header p {\n  font-size: 14px;\n  margin: 0 0 1rem;\n  color: #7a7a8c;\n}\n.card-header h2 {\n  font-size: 20px;\n  margin: 0.25rem 0 auto;\n  text-decoration: none;\n  color: inherit;\n  border: 0;\n  display: inline-block;\n  cursor: pointer;\n}\n.card-header h2:hover {\n  background: linear-gradient(90deg, #ff8a00, #e52e71);\n  text-shadow: none;\n  -webkit-text-fill-color: transparent;\n  -webkit-background-clip: text;\n  background-clip: text;\n}\n.card-author {\n  margin: 3rem 0 0;\n  display: grid;\n  grid-template-columns: 75px 1fr;\n  align-items: center;\n  position: relative;\n}\n.author-avatar {\n  grid-area: auto;\n  align-self: start;\n  position: relative;\n  box-sizing: border-box;\n}\n.author-avatar img {\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  filter: grayscale(100%);\n  display: block;\n  overflow: hidden;\n  margin: 16px 10px;\n}\n.author-name {\n  grid-area: auto;\n  box-sizing: border-box;\n}\n.author-name-prefix {\n  font-style: normal;\n  font-weight: 700;\n  color: #7a7a8c;\n}\n.half-circle {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 60px;\n  height: 48px;\n  fill: none;\n  stroke: #ff8a00;\n  stroke-width: 8;\n  stroke-linecap: round;\n  pointer-events: none;\n}\n.tags {\n  margin: 1rem 0 2rem;\n  padding: 0.5rem 0 1rem;\n  line-height: 2;\n  margin-bottom: 0;\n}\n.tags a {\n  font-style: normal;\n  font-weight: 700;\n  font-size: 0.5rem;\n  color: #7a7a8c;\n  text-transform: uppercase;\n  font-size: 0.66rem;\n  border: 3px solid #28242f;\n  border-radius: 2rem;\n  padding: 0.2rem 0.85rem 0.25rem;\n  position: relative;\n}\n.tags a:hover {\n  background: linear-gradient(90deg, #ff8a00, #e52e71);\n  text-shadow: none;\n  -webkit-text-fill-color: transparent;\n  -webkit-background-clip: text;\n  -webkit-box-decoration-break: clone;\n  background-clip: text;\n  border-color: white;\n}\n/*# sourceMappingURL=data:application/json;base64, */";